.header {
    position: fixed;
    z-index: 1;
    width: 100vw;
    height: 100px;
    top: 0;
    
    .logo__svg {
        fill: $color-primary;
    } 
}   

.header__logo-container{
    z-index: 1000;
    top: 30px;
    left: 40px;
    position: fixed;

    &--white {
        overflow: hidden;
        z-index: 10000;
        .logo__svg {
            fill: $color-white;
        }
    }
}